.container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
}


.footer {
    display: flex;
    list-style-type: none;
    margin-top: 100px;
    margin-left: 50px;
    // position: fixed;
    // right: 10px;
    // bottom: 0;
}


.btn {
    cursor: pointer;
    border: none;
    height: 63px;
    width: 63px;
    background-image: url(icon.png);
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 30px;
    opacity: 0.85;
}

.linkedin {
    margin-bottom: 18px;
    cursor: pointer;
    width: 58px;
    height: 58px;
    background-image: url(Linkedin.png);
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 30px;
    opacity: 0.85;
    transition: 0.5s;
}

.git {
    margin-bottom: 16px;
    cursor: pointer;
    width: 54px;
    height: 54px;
    background-image: url(github.png);
    background-repeat: no-repeat;
    background-size: cover;
    transition: 0.5s;
}

.email {
    margin-bottom: 14px;
    cursor: pointer;
    width: 54px;
    height: 54px;
    margin-left: 25px;
    background-image: url(email.png);
    background-repeat: no-repeat;
    background-size: cover;
    color: transparent;
    opacity: 0.85;
    transition: 0.5s;
}