.container {
  margin-left: 5vw;
  margin-right: 5vw;
  margin-top: 20px;
  min-width: 70vw;
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  max-height: 150vh;
  align-items: center;
  justify-content: center;
  background: rgba(7, 71, 120, 0.8);
    background: linear-gradient(0deg, rgba(7, 71, 120, 0.9) 38%, rgba(37, 115, 163, 0.8) 53%, rgba(127, 195, 208, 0.8) 73%, rgb(255, 255, 255, 0.9) 100%);
  background-position: top;
  border-radius: 15px;
}

.wrapper {
  display: flex;
  justify-content: center;
  margin-top: -20vh;
  height: 10vh;
  width: 110vw;
}

.hello {
  color:rgba(7, 71, 120, 0.8);
  text-shadow: 2px 2px rgb(255, 255, 255);
  font-family: 'Cormorant Garamond';
  font-size: calc(1vw + 1vh + 2.5vmin);
  letter-spacing: 0.7vw;
  white-space: nowrap;
  overflow: hidden;
  border-right: calc(0.1vw + 0vh + 0.1vmin) solid rgba(7, 71, 120, 0.8);
  animation: cursor 0.75s step-end infinite;
  width: 20ch;
  animation: cursor 0.75s step-end infinite, printedText 5s steps(15);
}

@keyframes cursor {
  0% {
    border-color:rgba(7, 71, 120, 0.8);
  }

  50% {
    border-color: transparent;
  }

  100% {
    border-color: rgba(7, 71, 120, 0.8);
  }
}

@keyframes printedText {
  from {
    width: 0;
  }
}

.homeBody {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: -50vh;
  justify-content: center;
}

@media screen and (max-width: 1500px) {
  .homeBody {
    // height: 600px;
    height: fit-content;

  }
}

.head2 {
  // margin-left: 50px;
  // margin-right: 50px;
  margin: 0 50px;
  padding-top: 50px;
  font-size: calc(2vw + 1vh + 1vmin);
  font-family: 'Cormorant Garamond';
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px rgba(10, 86, 119);
  text-align: center;
  line-height: 65px;

  @media screen and (max-width: 500px) {
    margin: 0 15px;
    font-size: x-large;
  }
}

.txt {
  margin-left: 30px;
  margin-right: 30px;
  font-family: 'Cormorant Garamond';
  font-size: calc(0.7vw + 0.7vh + 1vmin);
  color: rgb(247, 249, 250);
  text-shadow: 1px 1px rgb(3, 46, 68);
  text-align: center;
}

